import ShowBlogs from './components/ShowBlogs.vue'
import AddBlog from './components/AddBlog.vue'
import SingleBlog from './components/SingleBlog.vue'
import EditBlog from './components/EditBlog.vue'
export default[
	{path:"/Fyblog/",redirect:'/Fyblog/showblogs'},
	{path:"/Fyblog/showblogs",name:'showblogs',component:ShowBlogs},
	{path:"/Fyblog/add",component:AddBlog},
	{path:"/Fyblog/blog/:id",component:SingleBlog},
	{path:"/Fyblog/edit/:id",component:EditBlog}
]


